import { get, post, put, remove } from '@/api'

export default ({
    namespaced: true,
    state: {
        userList : [],
        userDetail : {}
    },
    mutations: {
        setuserList (state, arg) {
            state.userList = arg
        },
        setUserDetail (state, arg) {
            state.userDetail = arg
        },
    },
    actions: {
        fetchUsers ({ state, commit }) {
            return get('/users')
              .then(response => {
                console.log('response.data ', response.data.response.data.response)
                if (response && response.data && response.data.response && response.data.response.data) {
                  commit('setuserList', response.data.response.data)
                  let resp = response.data.response.data
                  for (let i in resp.users) {
                    resp.users[i].text = resp.users[i].name
                    resp.users[i].value = resp.users[i].id
                  }
                  return resp
                } else {
                  commit('setuserList', [])
                  return []
                }
              }).catch(error => error)
        },
        fetchUserDetail ({ state, commit }, payload) {
            return get(`/users/${payload}`)
              .then(response => {
                console.log('response.data ', response.data.response.data.response)
                if (response && response.data && response.data.response && response.data.response.data) {
                  commit('setUserDetail', response.data.response.data)
                  let resp = response.data.response.data
                  return resp && resp[0] ? resp[0] : {}
                } else {
                  commit('setUserDetail', [])
                  return []
                }
              }).catch(error => error)
        },
        addUser ({ state, commit}, payload) {
            console.log('payload ', payload)
            let {
                user_roles_id,
                username,
                password,
                status,
                last_edited_by = 1
            } = payload
            return post('/users', payload)
              .then(response => {
                  return response
              }).catch(error => error)
        },
        editUser ({ state, commit}, payload) {
            console.log('payload ', payload)
            let {
                user_roles_id,
                username,
                status,
                last_edited_by = 1,
                id
            } = payload
            return put(`/users/${payload.id}`, payload)
              .then(response => {
                  return response
              }).catch(error => error)
        },
        deleteUser ({ state, commit}, payload) {
            return remove(`/users/${payload}`, {})
              .then(response => {
                  return response
              }).catch(error => error)
        },
        resetAccount ({ state, commit }, payload) {
            return get(`/resetaccount/${payload.email}/?newusername=${payload.username}&newpassword=${payload.password}`)
              .then(response => {
                console.log('response.data ', response.data)
                if (response && response.data && response.data.response && response.data.response.data) {
                  let resp = response.data
                  return resp
                } else {
                  return {}
                }
              }).catch(error => error)
        },
        addUserInfo ({ state, commit}, payload) {
            console.log('payload ', payload)
            let {
                user_id,
                firstname,
                middlename,
                lastname,
                address_line_1,
                address_line_2,
                city,
                state_province,
                zip_postcode,
                country,
                contact_email,
                contact_mobile,
                contact_home,
                business_id,
                business_pos,
                last_edited_by
            } = payload
            return post('/userinfo', payload)
              .then(response => {
                  return response
              }).catch(error => error)
        },
        editUserInfo ({ state, commit}, payload) {
            console.log('payload ', payload)
            let {
                user_id,
                firstname,
                middlename,
                lastname,
                address_line_1,
                address_line_2,
                city,
                state_province,
                zip_postcode,
                country,
                contact_email,
                contact_mobile,
                contact_home,
                business_id,
                business_pos,
                last_edited_by
            } = payload
            return put(`/userinfo/${payload.id}`, payload)
              .then(response => {
                  return response
              }).catch(error => error)
        },
        deleteUserInfo ({ state, commit}, payload) {
            return remove(`/userinfo/${payload}`, {})
              .then(response => {
                  return response
              }).catch(error => error)
        },
        fetchUserInfoByUserId ({ state, commit }, payload) {
            return get(`/userinfobyuserid/${payload}`)
              .then(response => {
                console.log('response.data ', response.data.response.data.response)
                if (response && response.data && response.data.response && response.data.response.data) {
                  let resp = response.data.response.data
                  return resp
                } else {
                  return {}
                }
              }).catch(error => error)
        },
        
    }
})
