import Vue from 'vue'
import Vuex from 'vuex'
import axios from '@/api'


import user from './user'

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    user
  },
  state: {
    isLogin: false,
    isLoading: false,
    loginState: '',
    userData: {},
    error: {},
    isWidthForMobile: false,
    token: null
  },
  mutations: {
    setError(state, error) {
      console.log(error)
      state.error = error
    },
    setLoading(state, status) {
      state.isLoading = status
    },
    getLoginStatus() {},
    setUserData(state, payload) {
      state.userData = payload
    },
    setIsWidthForMobile(state, payload) {
      state.isWidthForMobile = payload
    },
    setToken(state, arg) {
      this.token = arg
    }
  },
  actions: {
    refreshToken({
      state
    }) {
      console.log('session')
    },
    async login({
      state,
      commit
    }, credentials) {
      console.log('credentials ', credentials)
      let basicAuth = 'Basic ' + btoa(credentials.username + ':' + credentials.password)
      console.log('basicAuth ', basicAuth);
      let response = await axios.post('/login', {
        ...credentials
      }, {
        headers: {
          Authorization: basicAuth
        }
      })
      commit('setToken', response.data.response.token)
      return response;
    },
    logout: function ({
      state
    }, session, router) {
      console.log('session ', session)
      console.log('router ', router)
      session.start();
      session.clear();
      session.destroy();
      return axios.post('/logout', {})
        .then(response => {
          session.destroy()
          return response
        }).catch(error => error)
    }
  }
})