import Axios from 'axios'
// Set host to dev api if localhost
// otherwise use the host address and prepend api
let hostname = window.location.hostname
let apiPort = 3001;
let apiVersion = '1.0';
let baseURL = undefined;
let devMode = false; // Please change this when switching server

if (devMode) {
  baseURL = `http://${hostname}:${apiPort}/api/${apiVersion}`
} else {
  baseURL = `http://178.128.102.191:5000/api/${apiVersion}`
}

let auth_token = null
if (window.localStorage.session) {
  auth_token = JSON.parse(window.localStorage.session).auth_token
  console.log('auth_token ', auth_token)
  console.log('baseURL ', baseURL)
} else {}

const axios = Axios.create({
  baseURL: baseURL,
  headers: {
    'Authorization': 'Bearer ' + auth_token,
    'Access-Control-Allow-Origin': '*'
  }
})

function get(url, options) {
  return axios.get(url, options)
    .then(response => {
      return response
    })
    .catch(err => err)
}

function post(url, data) {
  return axios.post(url, data)
    .then(response => response)
    .catch(err => err)
}

function put(url, data) {
  return axios.put(url, data)
    .then(response => {
      return response
    })
    .catch(err => {
      return err
    })
}

function remove(url, data) {
  return axios.delete(url, data)
    .then(response => {
      return response
    })
    .catch(err => {
      return err
    })
}

async function formPost(url, file) {
  const formData = new FormData()
  formData.append('file', file);
  return axios.post(url,
      formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }
    ).then(response => {
      console.log('response ', response)
      return {
        msg: 'Successfully Uploaded ',
        statusCode: 200,
        response: response.data.response
      }
    })
    .catch(function () {
      return {
        msg: 'Fail Uploading the file ',
        statusCode: 500
      }
    });
}

export default axios
export {
  get,
  post,
  put,
  remove,
  formPost
}