import Vue from 'vue'
import Router from 'vue-router'
import Meta from 'vue-meta'

import PageNotFound from '@/views/PageNotFound'
import Root from '@/components/Root'
import One from '@/components/CmsModule/One'
import Two from '@/components/CmsModule/Two'
import Three from '@/components/CmsModule/Three'

import Category from '@/components/CmsModule/Categories'
import CategoryList from '@/components/CmsModule/Categories/List'
import CategoryDetail from '@/components/CmsModule/Categories/Detail'

import Products from '@/components/CmsModule/Products'
import Services from '@/components/CmsModule/Services'
import Clients from '@/components/CmsModule/Clients'
import AboutUs from '@/components/CmsModule/AboutUs'


Vue.use(Router)
Vue.use(Meta)

const router = new Router({
  base: '/',
  mode: 'history',
  routes: [{
    path: '/',
    name: 'root',
    component: Root,
    redirect: '/three',
    children: [{
      path: '/components',
      name: 'components',
      component: One
    }, {
      path: '/two',
      name: 'two',
      component: Two
    }, {
      path: '/three',
      name: 'three',
      component: Three
    }, {
      path: '/category',
      name: 'category',
      component: Category,
      redirect: '/category',
      children: [{
        path: '/category/list',
        name: 'categorylist',
        component: CategoryList
      }, {
        path: '/category/:id/detail',
        name: 'categorydetail',
        component: CategoryDetail
      }]
    }, {
      path: '/products',
      name: 'products',
      component: Products
    }, {
      path: '/services',
      name: 'services',
      component: Services
    }, {
      path: '/clients',
      name: 'clients',
      component: Clients
    }, {
      path: '/abouts',
      name: 'abouts',
      component: AboutUs
    }]
  }, {
    path: '*',
    name: 'PageNotFound',
    component: PageNotFound
  }]
})

export default router